:root {
  --bg-orange: #f4c63d;
  --bg-brown-light: #eadbc4;
  --bg-brown-dark: #453d3f;
  --bg-brown-extra-dark: #272727;
  --bg-brown-extra-light: #ded0ba;
  --bg-red: #d70206;
}

*, :after, :before {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

body {
  color: var(--bg-brown-dark);
  background-color: var(--bg-brown-light);
  font-family: Oxygen, sans-serif;
  font-size: 1.6rem;
  font-weight: 400;
  line-height: 1.7;
  overflow: hidden;
}

ul {
  list-style: none;
}

.hidden {
  visibility: hidden;
  transition: all 1s ease-in;
  display: none;
}

main {
  display: flex;
}

.header {
  background-color: var(--bg-brown-dark);
  border-bottom: .3rem solid gray;
}

.header__list {
  margin-left: 2rem;
  list-style: none;
  display: flex;
}

.header__item {
  padding: .8rem;
}

.header__item:hover {
  background-color: var(--bg-brown-extra-dark);
}

.header__item a {
  color: var(--bg-brown-light);
  font-size: 1.4rem;
  text-decoration: none;
}

.sidebar {
  width: 35rem;
  height: 100vh;
  background-color: var(--bg-brown-extra-light);
  padding: 2rem 2rem 10rem 3rem;
  overflow-y: auto;
}

.sidebar__titles {
  cursor: pointer;
  font-size: 1.8rem;
  font-weight: 700;
  position: relative;
}

.sidebar__item {
  cursor: pointer;
  margin-left: 1.5rem;
  padding: .5rem;
  font-size: 1.2rem;
  position: relative;
}

.sidebar__list--1-1 {
  margin-left: 1.5rem;
}

.select:hover {
  color: var(--bg-brown-extra-light);
  background-color: var(--bg-brown-dark);
}

.menu {
  cursor: pointer;
}

.menu:after {
  content: "";
  width: 1rem;
  height: 1rem;
  background-image: url("up-arrow.dd8e7329.png");
  background-repeat: no-repeat;
  background-size: cover;
  display: block;
  position: absolute;
  top: 50%;
  left: -1rem;
  transform: translate(-50%, -50%)rotateZ(90deg);
}

.app-container {
  width: 100%;
  position: relative;
}

.welcome {
  width: 25rem;
  height: auto;
  color: #453d3f4d;
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -80%);
}

.welcome-icon {
  font-size: 12rem;
  animation: shake 2s ease-in-out infinite;
  display: block;
}

@keyframes shake {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.1);
  }

  100% {
    transform: scale(1);
  }
}

