:root{
	--bg-orange: #F4C63D;
	--bg-brown-light: #EADBC4;
	--bg-brown-dark: #453D3F;
	--bg-brown-extra-dark: #272727;
	--bg-brown-extra-light: #ded0ba;
	--bg-red: #D70206;
}

*,
*:after,
*:before{
	margin: 0;
	padding: 0;
	box-sizing: inherit;
}

html{
	font-size: 62.5%;
	box-sizing: border-box;
}

body{
	font-family: 'Oxygen', sans-serif;
	font-size: 1.6rem;
	font-weight: 400;
	line-height: 1.7;
	color: var(--bg-brown-dark);
	background-color: var(--bg-brown-light);
	overflow: hidden;
}

/* UTILS  */
ul{
	list-style: none;
}

.hidden{
	visibility: hidden;
	display: none;
	transition: 1s all ease-in;
}

main{
	display: flex;
}

/* HEADER */

.header{
	background-color: var(--bg-brown-dark);
	border-bottom: .3rem solid gray;
}

.header__list{
	list-style: none;
	display: flex;
	margin-left: 2rem;
}

.header__item{
	padding: .8rem;
}

.header__item:hover{
	background-color: var(--bg-brown-extra-dark);
}

.header__item a{
	font-size: 1.4rem;
	color: var(--bg-brown-light);
	text-decoration: none;
}

/* SIDEBAR  */

.sidebar{
	width: 35rem;
	height: 100vh;
	background-color: var(--bg-brown-extra-light);
	padding: 2rem 2rem 10rem 3rem;
	overflow-y: auto;
}

.sidebar__titles{
	font-weight: 700;
	font-size: 1.8rem;
	position: relative;
	cursor: pointer;
}

.sidebar__item{
	font-size: 1.2rem;
	margin-left: 1.5rem;
	padding: .5rem;
	cursor: pointer;
	position: relative;
}

.sidebar__list--1-1{
	margin-left: 1.5rem;
}

.select:hover{
	color: var(--bg-brown-extra-light);
	background-color: var(--bg-brown-dark);
}

.menu{
	cursor: pointer;
}

.menu:after{
	content: '';
	position: absolute;
	width: 1rem;
	height: 1rem;
	top: 50%;
	left: -1rem;
	display: block;
	transform: translate(-50%, -50%) rotateZ(90deg);
	background-image: url(../img/up-arrow.png);
	background-repeat: no-repeat;
	background-size: cover;
}

/* APP CONTAINER */

.app-container{
	width: 100%;
	position: relative;
}

.welcome{
	width: 25rem;
	height: auto;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%,-80%);
	color: rgba(69,61,63,.3);
	text-align: center;
}

.welcome-icon{
	display: block;
	font-size: 12rem;
	animation: shake 2s ease-in-out infinite;
}


@keyframes shake{
	0%{
		transform: scale(1);
	}

	50%{
		transform:  scale(1.1);
	}

	100%{
		transform: scale(1);
	}
}














